.header{
  position:fixed;
  top:0;
  z-index:100;
  width:100%;
  display:flex;
  justify-content: center;
  padding: 20px 0;
  background: linear-gradient(to bottom, rgba(0,0,0,.65) 0%,rgba(0,0,0,0) 100%);
}

.header a{
  margin: 0 20px;
  text-decoration:none;
  color:white;
  text-shadow: 2px 2px 5px rgba(0,0,0,.65);
}

@media only screen and (max-width:414px){
  .header{
    display:none;
  }
}
.banner{
  position:relative;
  display:flex;
  justify-content: center;
  min-height:100vh;
}

.banner__menu{
  display:none;
}

.banner__menuScreen{
  display:none;
}

.banner__container{
  width:100%;
  position:relative;
  display:grid;
  place-items: center;
}

.banner__container > img{
  width:100%;
  object-fit:cover;
  max-height:100vh;
  min-height:100vh;
  -webkit-filter:blur(2px) brightness(90%);
          filter:blur(2px) brightness(90%)
}

.banner__info{
  position: absolute;
  display:grid;
  place-items:center;
  z-index:99;
  padding:50px 0;
  transform: translateY(-50px);
}

.banner__name {  
  color:white;
  font-weight:bold;
  font-size:5.5rem;
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 1);
  letter-spacing:-2px;
}

.banner__info > h3 {
  font-family: 'Libre Baskerville', serif;
  color:#bbb;
  width:55%;
  line-height:1.5;
  font-weight:100;
  font-size:1.15rem;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 1);
  text-align:center;
  margin-top:10px;
}

.banner__info > h3 span{
  color:white;
}

.banner__socials{
  margin-top:70px;
  display:flex;
  justify-content: space-between;
  align-items: flex-end;
  width:15%;
  min-width: 195px;
}

.banner__down{
  color:white;
  position:absolute;
  z-index:99;
  bottom:50px;
  background-color:#11ABB0;
  display:flex;
  justify-content: center;
  align-items:center;
  height:50px;
  width:50px;
  transition: all .3s ease-in-out;
  cursor:pointer;
  border-radius:50%;
}

.banner__down:hover{  
  background-color:#39dcf1;
}

.banner__down > a{
  display:flex;
}

.banner__down > a > .MuiSvgIcon-root{
  font-size:2.5rem !important;
  color:rgb(12, 43, 41) !important;
  transition: all .3s ease-in-out;
}

.banner__down:hover > a > .MuiSvgIcon-root{
  color:white !important
}

.banner__fade{
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(24,24,24,1) 100%);
  height:400px;
  width:100%;
  position:absolute;
  bottom:-5px;
}

@media only screen and (max-width: 992px){
  .banner__name {  
    font-size:4rem;
    display:flex;
    flex-direction:column;
    align-items:center;
    line-height:1.1;
    margin-bottom:30px;
  }

  .banner__name > span{  
